header {
  width: 100%;
  height: auto;
  color: white;
  position: relative;
  overflow: hidden;
  text-align: center;
  perspective: 100px;
  background-color: #0d1323;
  background-image: radial-gradient(ellipse at 50% 36%, #142c63 0, #0d1323 45%, #000 150%); }

.branding {
  height: 101vh;
  width: 100%;
  padding: 0 5rem;
  z-index: 15;
  display: flex;
  position: relative;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: transparent; }
  @media screen and (max-width: 800px) {
    .branding {
      height: 80vh; } }

.pronounce {
  padding-bottom: 0.5rem;
  position: relative;
  font-size: 11px;
  z-index: 5;
  font-weight: lighter;
  opacity: 0;
  transition: all 500ms ease;
  pointer-events: none;
  display: inline-block;
  transform: scaleX(0.9); }
  .pronounce i {
    font-size: 12px;
    color: white;
    text-shadow: 0 -5px 20px rgba(0, 0, 0, 0.3), 0 -5px 20px rgba(0, 0, 0, 0.3); }

.explanation {
  margin-top: -0.5rem;
  color: white;
  border-radius: 12px;
  padding: 0.45rem 1.5rem 0.35rem;
  font-style: italic;
  border-top: 1px solid rgba(80, 220, 255, 0.7); }
  .explanation span {
    font-size: 12px;
    font-style: normal; }

.hoverwrap {
  margin-top: -2rem; }
  .hoverwrap:hover .pronounce {
    opacity: 1;
    pointer-events: auto;
    transform: scaleX(1); }
  .hoverwrap:hover h1 span {
    padding: 0 10px;
    color: #fff;
    text-align: center;
    text-transform: uppercase;
    font-family: "aquatico", sans-serif;
    font-weight: 200;
    letter-spacing: 8px;
    text-shadow: 0 0 5px #fff, 0 0 20px #60c0f0, 0 0 50px #60c0f0;
    transform: translateY(0px);
    cursor: url("./../ram-logo-white.png"), auto; }

h1 {
  font-family: "Aquatico";
  font-size: 4.75rem;
  line-height: 100%;
  text-transform: uppercase; }
  @media screen and (max-width: 400px) {
    h1 {
      font-size: 3.75rem; } }
  @media screen and (min-width: 568px) {
    h1 {
      font-size: 8rem;
      margin: 0; } }
  h1 span {
    text-shadow: 0 0 05px white, 0 0 15px white;
    transition: all 300ms ease; }

h2 {
  display: flex;
  width: 100%;
  margin-top: 0.75rem;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between; }
  h2 .skills {
    display: flex;
    padding: 0.5rem 1rem;
    justify-content: center;
    font-size: 1.25rem;
    letter-spacing: 5px;
    text-transform: uppercase;
    flex-direction: column;
    color: #fff;
    flex: 1;
    margin-bottom: -40px;
    font-weight: bold;
    font-family: "Aquatico";
    transition: transform 300ms ease;
    background-color: white;
    background-image: linear-gradient(-180deg, white 40%, #99d4f1 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent; }
    @media screen and (max-width: 960px) {
      h2 .skills {
        margin: 0; } }
    @media screen and (max-width: 540px) {
      h2 .skills {
        font-size: 0.85rem; } }
    h2 .skills div {
      flex-grow: 1;
      padding: 0 2rem;
      text-align: left; }
    h2 .skills span {
      display: block;
      font-family: "Barlow";
      letter-spacing: 0.25rem;
      font-size: 0.75rem;
      margin-bottom: 0px;
      font-weight: normal; }
      @media screen and (max-width: 540px) {
        h2 .skills span {
          font-size: 0.6rem; } }

.space {
  width: 1500px;
  height: 1500px;
  border-radius: 50%;
  overflow: hidden;
  position: absolute;
  top: 38%;
  left: 50%;
  perspective: 180px;
  animation: spin 150s linear infinite;
  transform-origin: center center;
  pointer-events: none; }
  .space .stars {
    display: block;
    position: absolute;
    border-radius: 50%;
    background: #fff; }
  @media screen and (min-width: 1600px) {
    .space {
      width: 2000px;
      height: 2000px; } }

