.eyebrowMenu {
  position: absolute;
  top: 20px;
  padding: 1.25rem 3.5rem;
  left: 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between; }
  @media screen and (max-width: 800px) {
    .eyebrowMenu {
      padding: 1.25rem 1.5rem; } }
  .eyebrowMenu span {
    position: absolute;
    font-size: 12px;
    transform: translateX(calc(-100% + 10px)) translateY(-16px);
    display: inline-block;
    width: max-content;
    text-align: right;
    opacity: 0;
    padding: 20px 0.5rem;
    transition: opacity 500ms ease, transform 500ms ease; }
  .eyebrowMenu audio {
    display: none; }
  .eyebrowMenu button {
    background: none;
    cursor: pointer;
    color: white;
    font-size: 15px;
    padding: 15px 16px;
    width: 52px;
    height: 52px;
    margin-top: 0.5rem;
    border: 1px solid transparent;
    left: 0;
    border-radius: 50px; }
    .eyebrowMenu button:hover span {
      opacity: 1;
      transform: translateX(calc(-100% - 20px)) translateY(-16px); }
    .eyebrowMenu button.addStar {
      box-shadow: 0 0 3px #fff493; }
      .eyebrowMenu button.addStar:active {
        background-color: #fff066; }
    .eyebrowMenu button#pauseButton {
      top: 60px;
      box-shadow: 0 0 3px #2bf868;
      display: none; }
    .eyebrowMenu button#playButton {
      opacity: 1;
      top: 60px;
      box-shadow: 0 0 3px #41c1fd; }
      .eyebrowMenu button#playButton:active {
        background-color: #41c1fd; }
      .eyebrowMenu button#playButton i {
        margin-left: -2px; }

@keyframes spin {
  0% {
    transform: translateX(-50%) translateY(-50%) rotate(360deg); }
  100% {
    transform: translateX(-50%) translateY(-50%) rotate(0deg); } }

@keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }
