.menu-trigger {
  cursor: pointer;
  background: none;
  border: none;
  padding: 0;
  text-align: center;
  padding: 22px 12px; }
  .menu-trigger:hover span::after {
    transform: translateX(0%); }
  .menu-trigger * {
    transition: all 500ms cubic-bezier(0.175, 0.885, 0.32, 1.275); }
  .menu-trigger span {
    display: block;
    border-radius: 4px;
    width: 46px;
    height: 5px;
    overflow: hidden;
    position: relative;
    background-color: #3178d5;
    background-image: linear-gradient(30deg, #70d5ff, #0059bc);
    box-shadow: 1px 3px 3px 0 rgba(30, 149, 247, 0.25); }
    .menu-trigger span:not(:first-child) {
      margin-top: 10px; }
    @media screen and (min-width: 800px) {
      .menu-trigger span {
        width: 48px; } }
    .menu-trigger span::after {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      background-color: #3fc3f7;
      left: 0;
      top: 0;
      transform: translateX(-100%); }
    .menu-trigger span:nth-of-type(1)::after {
      transition: transform 300ms ease 200ms; }
    .menu-trigger span:nth-of-type(2)::after {
      transition: transform 400ms ease 400ms; }
    .menu-trigger span:nth-of-type(3)::after {
      transition: transform 500ms ease 600ms; }

